@import url(https://fonts.googleapis.com/css?family=Open+Sans|Libre+Lora);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:focus {
  outline: none;
}

.App {
  scroll-behavior: smooth;
  min-height: 100vh;
}

.App-header {
  /*background-color: #282c34;*/
  background-color: #fafafa;
  height: 10vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.cm-s-cherry {
  padding: 0 0 0 10px;
  font-size: 1em;
  line-height: 1.5em;
  /*font-family: inconsolata, monospace;*/
  font-family: 'Open Sans', sans-serif;
  letter-spacing: 0.3px;
  word-spacing: 1px;
  /*background: rgba(234, 255, 208, 0.4);*/
  background: rgba(255, 238, 204, 0.5);
  color: #282c34;
}

.cm-s-cherry .cm-header {
  color: #F25565;
  /*font-family: 'Lora', serif;*/
}

.cm-s-cherry .CodeMirror-lines {
  padding: 4px 0;
}

.cm-s-cherry .CodeMirror-gutters {
  box-shadow: 1px 0 2px 0 rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 1px 0 2px 0 rgba(0, 0, 0, 0.5);
  background-color: #010304;
  padding-right: 10px;
  z-index: 3;
  border: none;
}
.cm-s-cherry div.CodeMirror-cursor {
  border-left: 2px solid #aaa;
}

.cm-s-cherry .CodeMirror-activeline-background {
  background: #ffebbd;
}

.cm-s-cherry .CodeMirror-selected {
  background: #ffebbd;
}

/* code, code block */
.cm-s-cherry .cm-comment {
  color: #999;
}

/* list level 1 */
.cm-s-cherry .cm-variable-2 {
  color: #A94CAF;
}

/* list level 2 */
.cm-s-cherry .cm-variable-3 {
  color: #11CD86;
}

/* list level 3 */
.cm-s-cherry .cm-keyword {
  color: #fa8e82;
}

/* url or image link */
.cm-s-cherry .cm-string {
  color: #0366d6;
  /*color: #0080ff;*/
  /*color: #f2671f;*/
}

.cm-s-cherry .cm-quote {
  font-style: italic;
}

/* check-box */
.cm-s-cherry .cm-property {
  color: gray;
}

.cm-s-cherry .cm-atom {
  color: #DAD085;
}

.cm-s-cherry .cm-number {
  color: #DAD085;
}

.cm-s-cherry .cm-operator {
  color: #FF4A1A;
}
.cm-s-cherry .CodeMirror-linenumber {
  color: black;
}

/* Custom scrollbar style */

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background-color: #ddd;
}

/* global setting */

h1, h2, h3, h4, h5, h6 {
  font-family: 'Lora', serif;
}
p, ul, li, a, table {
  font-family: 'Open Sans', sans-serif;
}

/* code mirror setting */
.CodeMirror {
  height: auto;
}

